import { component } from '../../setup';
import { button } from '../button';
import { mode } from './modes';
import { prefix, properties } from './properties';
import { ModalFooterButtonPosition, ModalSize } from './types';
export const modalBase = component({
    window: {},
    header: {
        title: {},
        subTitle: {}
    },
    body: {},
    footer: {
        [ModalFooterButtonPosition.Left]: {},
        [ModalFooterButtonPosition.Right]: {}
    },
    backdrop: {},
    displayNone: {},
    shadow: {},
    [ModalSize.Small]: {},
    [ModalSize.Medium]: {},
    [ModalSize.Large]: {}
}, {
    className: prefix
});
export const modalWindow = {
    color: mode.text.color,
    zIndex: properties.base.zIndex,
    backgroundColor: mode.background
};
export const modalHeader = {
    borderBlockEndStyle: properties.header.borderBlockEndStyle,
    borderBlockEndWidth: properties.header.borderBlockEndWidth,
    borderBlockEndColor: mode.borderColor,
    display: properties.header.display,
    position: properties.header.position,
    justifyContent: properties.header.justifyContent,
    alignItems: properties.header.alignItems,
    [`& .${modalBase.header.title.$}`]: {
        marginBlock: 0,
        marginInline: 0,
        paddingBlock: 0,
        paddingInline: 0
    },
    [`& .${modalBase.header.subTitle.$}`]: {
        marginBlockEnd: 0,
        marginInline: 0,
        paddingBlock: 0,
        paddingInline: 0
    },
    [`& .${button.$}`]: {
        border: properties.header.button.border,
        backgroundColor: properties.header.button.backgroundColor,
        outline: properties.header.button.outline,
        alignItems: properties.header.button.alignItems,
        fontSize: properties.header.button.size,
        paddingBlockStart: properties.header.button.paddingBlockStart,
        paddingInlineEnd: properties.header.button.paddingInlineEnd
    }
};
export const modalBody = {
    textAlign: properties.body.textAlign
};
export const modalFooter = {
    borderTopStyle: properties.footer.borderTopStyle,
    borderTopWidth: properties.footer.borderTopWidth,
    borderTopColor: mode.borderColor,
    display: properties.footer.display,
    gap: properties.footer.gap,
    [`& .${button.$}`]: {
        outline: properties.footer.button.outline
    }
};
export const modalContainer = {
    color: properties.container.color,
    borderWidth: properties.container.borderWidth,
    position: properties.container.position,
    insetBlockStart: properties.container.insetBlockStart,
    insetInlineStart: properties.container.insetInlineStart,
    inlineSize: properties.container.inlineSize,
    display: properties.container.display,
    justifyContent: properties.container.justifyContent
};
export const modalBackdrop = {
    position: properties.backdrop.position,
    zIndex: properties.backdrop.zIndex,
    insetInlineStart: properties.backdrop.insetInlineStart,
    insetBlockStart: properties.backdrop.insetBlockStart,
    inlineSize: properties.backdrop.inlineSize,
    blockSize: properties.backdrop.blockSize,
    backgroundColor: properties.backdrop.backgroundColor
};
export const modal = modalBase.fork({
    borderWidth: properties.container.borderWidth,
    position: properties.container.position,
    insetBlockStart: properties.container.insetBlockStart,
    insetInlineStart: properties.container.insetInlineStart,
    inlineSize: properties.container.inlineSize,
    display: properties.container.display,
    justifyContent: properties.container.justifyContent,
    [`& .${modalBase.window.$}`]: modalWindow,
    [`& .${modalBase.window.$} .${modalBase.header.$}`]: modalHeader,
    [`& .${modalBase.window.$} .${modalBase.body.$}`]: modalBody,
    [`& .${modalBase.window.$} .${modalBase.footer.$}`]: modalFooter,
    [`& .${modalBase.shadow.$}`]: {
        boxShadow: `${properties.shadow.x} ${properties.shadow.y} 
        ${properties.shadow.blur} ${mode.shadowColor}`
    },
    ...Object.values(ModalFooterButtonPosition).reduce((acc, position) => ({
        ...acc,
        [`& .${modalBase.window.$} .${modalBase.footer[position].$}`]: {
            justifyContent: properties.footer[position].justifyContent
        }
    }), {}),
    size: {
        ...Object.values(ModalSize).reduce((acc, size) => ({
            ...acc,
            [size]: {
                [`& .${modalBase.window.$}`]: {
                    inlineSize: properties[size].size
                },
                [`& .${modalBase.window.$} .${modalBase.header.$} .${modalBase.header.title.$}`]: {
                    fontSize: properties[size].header.titleFontSize
                },
                [`& .${modalBase.window.$} .${modalBase.header.$} .${modalBase.header.subTitle.$}`]: {
                    fontSize: properties[size].header.subTitleFontSize
                },
                [`& .${modalBase.window.$} .${modalBase.header.$} .${button.$}`]: {
                    fontSize: properties[size].header.titleFontSize
                },
                [`& .${modalBase.window.$} .${modalBase.header.$}`]: {
                    marginInline: properties[size].gapLarge,
                    marginBlockStart: properties[size].gapLarge,
                    marginBlockEnd: properties[size].gapSmall,
                    paddingBlockEnd: properties[size].gapSmall
                },
                [`& .${modalBase.window.$} .${modalBase.body.$}`]: {
                    marginInline: properties[size].gapLarge,
                    marginBlockStart: properties[size].gapSmall,
                    marginBlockEnd: properties[size].gapSmall,
                    fontSize: properties[size].body.fontSize
                },
                [`& .${modalBase.window.$} .${modalBase.footer.$}`]: {
                    marginBlockStart: properties[size].gapSmall,
                    marginInline: properties[size].gapLarge,
                    marginBlockEnd: properties[size].gapLarge,
                    paddingBlockStart: properties[size].gapSmall
                }
            }
        }), {})
    },
    backdrop: modalBackdrop,
    displayNone: {
        display: 'none'
    }
});
