import { palette } from '../../palette';
import { ThemeMode, token } from '../../setup';
import { FormVariant } from './types';
export const mode = token({
    color: palette.themeLightGray900,
    backgroundColor: palette.themeLightGray50,
    [FormVariant.Ghost]: {
        backgroundColor: palette.transparent
    }
}, {
    prefix: 'spark-form'
});
export const modeDark = token({
    color: palette.themeLightGray900,
    backgroundColor: palette.themeLightGray50,
    [FormVariant.Ghost]: {
        backgroundColor: palette.transparent
    }
});
export const modes = {
    [ThemeMode.Light]: mode,
    [ThemeMode.Dark]: modeDark
};
