import { palette } from '../../palette';
import { ThemeMode, token } from '../../setup';
import { prefix } from './properties';
export const mode = token({
    backgroundPrimary: palette.classicBlue,
    unvisitedColor: palette.carbon,
    visitedColor: palette.classicBlue,
    activeColor: palette.classicBlue,
    white: palette.white,
    textColor: palette.themeDarkGray50
}, {
    prefix: prefix
});
export const darkMode = mode.fork({
    backgroundPrimary: palette.themeDarkGray50,
    unvisitedColor: palette.carbon,
    visitedColor: palette.energyBlue,
    activeColor: palette.energyBlue,
    white: palette.white,
    textColor: palette.themeLightGray50
});
export const modes = {
    [ThemeMode.Light]: mode,
    [ThemeMode.Dark]: darkMode
};
