import { component } from '../../setup';
import { mode } from './modes';
import { prefix, properties } from './properties';
import { StepperOrientation } from './types';
const stepperBase = component({
    stepVisited: {},
    stepActive: {},
    stepContainer: {},
    stepButton: {},
    stepText: {},
    step: {},
    orientation: {
        [StepperOrientation.Vertical]: {},
        [StepperOrientation.Horizontal]: {}
    }
}, {
    className: prefix
});
export const stepper = stepperBase.fork({
    display: 'flex',
    [`& .${stepperBase.step.$}:first-child .${stepperBase.stepContainer.$}::before, & 
    .${stepperBase.step.$}:last-child .${stepperBase.stepContainer.$}::after`]: {
        display: 'none'
    },
    [`& .${stepperBase.step.$}.${stepperBase.stepActive.$} .${stepperBase.stepButton.$}, 
    .${stepperBase.step.$}.${stepperBase.stepVisited.$} .${stepperBase.stepButton.$}`]: {
        backgroundColor: mode.activeColor
    },
    [`& .${stepperBase.step.$}.${stepperBase.stepActive.$} .${stepperBase.stepContainer.$}::before, 
    .${stepperBase.step.$}.${stepperBase.stepVisited.$} .${stepperBase.stepContainer.$}::before, 
    .${stepperBase.step.$}.${stepperBase.stepVisited.$} .${stepperBase.stepContainer.$}::after`]: {
        backgroundPositionX: '-100%'
    },
    [`& .${stepperBase.stepActive.$} .${stepperBase.stepContainer.$}::before, & 
    .${stepperBase.stepActive.$} .${stepperBase.stepContainer.$}::after`]: {
        transitionDelay: properties.animationSpeed
    },
    [`& .${stepperBase.step.$}`]: {
        display: 'flex',
        flex: 1,
        flexDirection: 'column',
        alignItems: 'center'
    },
    [`& .${stepperBase.stepContainer.$}`]: {
        position: 'relative',
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        '&::before,&::after': {
            content: '" "',
            position: 'absolute',
            top: `calc(${properties.step.size} / 2)`,
            background: `linear-gradient(90deg,
                ${mode.unvisitedColor} 50%,
                ${mode.activeColor} 50%
            )`,
            backgroundSize: '200%',
            backgroundPositionX: '0%',
            transition: `background-position-x ${properties.animationSpeed} linear`,
            height: properties.connector.size
        },
        '&::before': {
            left: 0,
            paddingRight: `calc(50% - ${properties.step.size} * ${properties.connector.gapFactor})`
        },
        '&::after': {
            right: 0,
            paddingLeft: `calc(50% - ${properties.step.size}  * ${properties.connector.gapFactor})`
        }
    },
    [`& .${stepperBase.stepButton.$}`]: {
        width: properties.step.size,
        height: properties.step.size,
        borderRadius: `calc( ${properties.step.size} /2 )`,
        border: 'none',
        backgroundColor: mode.unvisitedColor,
        transition: `background-color ${properties.animationSpeed} linear 0.4s`,
        color: mode.white
    },
    [`& .${stepperBase.stepText.$}`]: {
        color: mode.textColor,
        padding: properties.step.textPadding,
        textAlign: 'center'
    },
    [`&.${stepperBase.orientation.horizontal.$}`]: {
        flexDirection: 'row'
    },
    [`&.${stepperBase.orientation.vertical.$}`]: {
        flexDirection: 'column'
    }
});
